@import url(https://fonts.googleapis.com/css?family=Shrikhand);
@import url(https://fonts.googleapis.com/css2?family=Montserrat);
@import url(https://fonts.googleapis.com/css?family=Karla);
@import url(https://fonts.googleapis.com/css?family=Asap);
@import url(https://fonts.googleapis.com/css?family=Hind);
@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App-logo {
    font-family: Shrikhand !important;
    font-size: 75px !important;
    margin-left: 45px !important;
    /* color: #E3B7A6 !important; */
    color: #FB6D8E !important;
  }

.topnav > a, .dropdown > .drop, .dropdown-content  {
    margin: 0px 30px;
    font-size: 15px;
    font-family: Open Sans;
    font-weight: 600;
    color: #6B6D76;
    position: relative;
}

.topnav > a:hover, .dropdown-content > a:hover, .dropdown:hover{
    text-decoration: underline;
    
}

.sticky-nav {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
  }

.icon {
    margin: 0 20px;
    color: #6B6D76;
}

.icons {
    float: right;
    margin: 60px 45px 0px 0px;
}

.ico {
    padding: 33px 45px 0 0;
    color: #6B6D76;
    font-size: 35px;
    text-decoration: none;
    float: right;
    display: none;
  }

  .dropdown-content a{
    padding: 10px 0 0 14px;
    display: block !important;
  }

  .dropdown:hover .dropdown-content {display: block;}

.dropdown{
  display: inline-block !important;
}

.navbar{
  background-color: #F8F9FA;
}
footer {
    /* text-align: center; */
    /* padding: 40px 0; */
    /* display: inline; */
    overflow: hidden;
    /* margin-bottom: 40px; */
}

.bottom-nav{
    float: left;
    /* text-align: left; */
    padding-left: 30px;
}

.copyright{
    /* text-align: right; */
    float: right;
    padding-right: 30px;
}
/* .Home {
    padding: 20px 0;
} */

.Home, .links, .Name {
    padding: 0 10%;
    font-size: 20px;
    text-align: center;
}

.Name {
 font-size: 60px;
 margin: 0 !important;
 margin-top: 100px !important;
 font-family: Hind;
}

.top {
    height: 270px;
}


.links {
    font: 25px;
    margin: 0 !important;

}

.links > a{
    padding: 0 10px;
}

.Projects{
    text-align: left;
}

.Proj{
    display: grid;
    /* padding: 5% 15%; */
    grid-template-columns: 1.5fr 1fr;
    align-items: center;
    padding: 0 10px !important;
}

/* .about {
    padding: 75px 150px;
} */

img {
    width: 100%;
}

.info {
    display: grid;
    /* padding: 5% 15%; */
    grid-template-columns: .65fr 1.5fr;
    align-items: center;
    padding: 0 10px !important;
}

h1 {
    font-size: 2vw;
}

.details{
    padding: 0% 10%;
    font-size: 1.5vw;
}

.Icons {
    margin: 45px 0 0;
    text-align: center;
    font-size: 30px;
}


/* .icon{
    width: 150%;
} */
.Home, .about {
  padding: 75px 150px;
}

a {
  text-decoration: none;
  color: inherit;
}

p {
  font-family: Open Sans;
}

p, h1 {
  color: #6B6D76;
}

.contact > h1, .Projects-Header {
  font-family: Hind;
  font-size: 40px;
}

.contact, .Projects {
  padding: 75px 0 0;
}

@media screen and (max-width: 850px) {
  .Home, .about {
    padding: 50px;
  }

  .Name {
    font-size: 7vw;
  }

  .links {
    font-size: 2vw;
  }

  .contact > h1 {
    font-size: 5vw;
  }

  #myTopnav {display: none;}
  .topnav > a {
      display: block !important;
      text-align: center;
      margin: 0px 0px 20px;
      font-size: 17px;
  }
  .ico {
    display: inline-block;
  }
  .topnav.responsive {
    display: block !important;
  }
}

.dropdown-content {
  display: none;
  position: absolute;
  min-width: 160px;
  z-index: 1;
}
